import React, {useState, useEffect} from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import {Button, Heading, HStack, Fade, Box} from '@chakra-ui/react'
import { Swiper, SwiperSlide } from 'swiper/react';


export default function TagSelector({handleCategories}) {
    const [scrollActive, setScrollActive] = useState(false);

    useEffect(()=>{
      window.addEventListener('scroll',()=>{
        if(window.scrollY >= 100){
          setScrollActive(true);
        }else{
          setScrollActive(false);

        }
      })
    })
    const {distinct, group, totalCount} = useStaticQuery(graphql`
    {
        allMarkdownRemark {
        distinct(field: frontmatter___category)
        totalCount
        group(field: frontmatter___category){
                totalCount
        }
        }
    }
    `).allMarkdownRemark;
    const Tags = distinct.map((name, idx) => [`${name}(${group[idx].totalCount})`, name]);
    Tags.unshift([`ALL(${totalCount})`, 'ALL']);
    // console.log(distinct, group, totalCount, Tags);
  return (
    <>
    <Heading as={'h2'}> {totalCount}  .</Heading>
    <HStack w={'lg'} flexWrap={'wrap'} py={4}>
        {Tags.map(tag=><Button
         key={tag[0]}
         borderRadius={10}
         shadow={'md'}
         value={tag[1]}
         onClick={handleCategories}
         >
        {tag[0]}
        </Button>)}
    </HStack >
    <Box bg={"gray.500"}>
      <Swiper
      slidesPerView={1}
      direction='horizontal'>
        <SwiperSlide>
          <Button>1</Button>
        </SwiperSlide>
        <SwiperSlide>
          <Button>1</Button>
        </SwiperSlide><SwiperSlide>
          <Button>1</Button>
        </SwiperSlide>
      </Swiper>
    </Box>
    {/* <Swiper 
    direction="vertical"
    spaceBetween={50}
    slidePerView={5} 
    onSlideChange={() => console.log('slide change')}
    onSwiper={(swiper) => console.log(swiper)}
    >
     {Tags.map(tag=>
     <SwiperSlide>
     <Button>{tag[0]}</Button>
    </SwiperSlide>
     )}
  </Swiper> */}


    {scrollActive&&
    <Fade in={scrollActive}>
       <HStack w={'2xl'} flexWrap={'wrap'} py={4} pos="fixed" top={1}>
          {Tags.map(tag=><Button
          key={tag[0]}
          borderRadius={10}
          shadow={'md'}
          value={tag[1]}
          onClick={handleCategories}
          >
          {tag[0]}
          </Button>)}
      </HStack >
    </Fade>}

    </>
  )
}